import c from './MyPost.module.scss';

export const MyPost = (props) => {
	// console.log(props)
	return(
		<div className={c.post}>
			<div className={c.post__container}>
				<div className={c.post__avatar}>
					<img alt="avatar" src="data:image/jpeg;base64," />
				</div>
				<div className={c.post__content}>
					<PostTitle type={props.type}/>
					<div className={c.post__body}>
						<div className={c.post__text}>{props.message}</div>
					</div>
					<a href='#' className={c.post__likes}>{props.number} likes</a>
				</div>
			</div>
		</div>
	)
}

export const PostTitle = (props) => {
	return (
		<div className={c.post__top}>
			<div className={c.post__title}>
				<span> </span> {props.type}
			</div>
			<div className={c.post__time}>14 hours <span> ago</span></div>
		</div>
	)
}